:root {
  --nokia-blue: #124191;
  --nokia-blue-black: 001135;
  --gray-1: #edf3f5;
  --gray-2: #bec8d2;
  --gray-3: #98a2ae;
}

body {
  font-family: Arial, Helvetica Neue, Helvetica, sans-serif;
}

.navBar {
  background-color: var(--gray-1);
  margin-bottom: 2rem;
}

.navBar a {
  color: var(--nokia-blue);
  text-decoration: none;
}

.navBar a:hover {
  color: var(--nokia-blue-black);
}

.repoList a {
  text-decoration: none;
  color: var(--nokia-blue-black);
}
